er-radius: 5px;\n    }\n\n    span{\n        font-size: 12px;\n        color: red;\n    }\n\n    .nav-button{\n        color:$primary-color;\n        transition: all 150ms ease-in-out;\n\n        &:hover{\n            color:$secondary-color;\n        }\n\n        &:focus{\n            box-shadow: none!important;\n        }\n    }\n\n    body {\n        color: $primary-color;\n        font-family: $primary-font;\n        overflow-x: hidden;\n        height: 100%;\n        background-image: none;\n        background-repeat: no-repeat\n    }\n\n    input,\n    textarea {\n        background-color: none;\n        border-radius: 7px !important;\n        padding: 12px 15px;\n        width: 100%;\n        box-sizing: border-box;\n        border: 1px solid $primary-color !important;\n        font-size: 16px;\n        color: $primary-color;\n        font-weight: 400\n    }\n\n    input:focus,\n    textarea:focus {\n        -moz-box-shadow: none !important;\n        -webkit-box-shadow: none !important;\n        box-shadow: none !important;\n        border: 1px solid $primary-color !important;\n        outline-width: 0;\n        font-weight: 400\n    }\n\n    .main-btn:focus {\n        -moz-box-shadow: none !important;\n        -webkit-box-shadow: none !important;\n        box-shadow: none !important;\n        outline-width: 0\n    }\n\n    .card {\n        border-radius: 0;\n        border: none;\n        width: 100%;\n    }\n\n\n    .heading {\n        margin-bottom: 60px !important\n    }\n\n    ::placeholder {\n        color: #000 !important;\n        opacity: 0.5\n    }\n\n    :-ms-input-placeholder {\n        color: $primary-color !important\n    }\n\n    ::-ms-input-placeholder {\n        color: $primary-color !important\n    }\n\n    .form-control-label {\n        font-size: 12px;\n        margin-left: 15px\n    }\n\n    .msg-info {\n        font-family: $bold-font;\n        color: $primary-color;\n        margin-bottom: 30px;\n    }\n\n    .btn-color {\n        font-family: $bold-font;\n        border-radius: 7px;\n        color: #fff;\n        background-color: $primary-color;\n        padding: 7px 15px;\n        cursor: pointer;\n        border: none !important;\n        // margin-top: 40px\n    }\n\n    .btn-color:hover {\n        color: #fff;\n        background-color: $secondary-color;\n    }\n\n    .btn-white {\n        // border-radius: 50px;\n        color: $quinary-color;\n        background-color: none;\n        // padding: 8px 40px;\n        cursor: pointer;\n        border: none;\n        text-decoration: underline;\n    }\n\n    .btn-white:hover {\n        color: $quartery-color;\n        background-image: none;\n\n    a {\n        color: #000\n    }\n\n    a:hover {\n        color: #000\n    }\n\n    .bottom {\n        width: 100%;\n        margin-top: 50px;\n\n    }\n\n    .sm-text {\n        font-size: 15px;\n    }\n\n\n}\n\n"],"sourceRoot":""}]);
